import React, { Component } from 'react';
import globalStyles from '../../common/style';
import { Link } from 'react-router-dom';
import { withRouter } from "react-router";
import moment from "moment";

class ReportsScreen extends Component {
    state = {
        localStorageKeys: [],
        localStorageData: {},
        newReportID: this.randomStringGenerator()
    }
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        this.localStorageHandlerForState()
    }

    randomStringGenerator(length = 8) {
        const chars = "abcdefghijklmnoprstuvyz";
        let result = "";
        for (let i = 0; i < length; i++) result += chars.charAt(Math.floor(Math.random() * chars.length));
        return result;
    }
    randomNumberGenerator(length = 8) {
        const nums = "0123456789";
        let result = "";
        for (let i = 0; i < length; i++) result += nums.charAt(Math.floor(Math.random() * nums.length));
        return result;
    }

    localStorageHandlerForState() {
        const items = { ...localStorage };
        let keys = Object.keys(items);
        const allDataKeysSaved = keys.filter((key, i) => {
            if (key.includes("_report_")) {
                return true
            } else {
                return false
            }
        })
        const localStorageData = items;
        const finalData = {};
        allDataKeysSaved.reverse().forEach((item, index) => {
            finalData[item] = JSON.parse(localStorageData[item])
        })
        this.setState({
            localStorageKeys: allDataKeysSaved,
            localStorageData: finalData
        })
    }
    startANewReport() {
        return (
            <a role="a" target="__blank" href={`report/${this.state.newReportID}`} style={{
                width: '100%',
                textAlign: "center",
                height: "100px",
                margin: "auto",
                marginBottom: "15px",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                fontSize: "25px",
                fontFamily: "'Rubik',sans-serif",
                backgroundColor: globalStyles.bartyDark,
                color: globalStyles.white,
                textDecoration: "none"
            }}>
                Yeni Rapor Başlat
            </a>
        )
    }
    renderReports({ key }) {
        return (
            <div class="col-lg-3 col-md-6 col-sm-12"
                style={{
                    height: "150px",
                    marginTop: "15px"
                }}>
                <button onClick={() => {
                    localStorage.removeItem(key);
                    this.localStorageHandlerForState()
                }} style={{
                    position: "absolute",
                    right: "20px",
                    top: "5px",
                    backgroundColor: globalStyles.white,
                    color: globalStyles.bartyDark,
                    outline: "none",
                    width: "40px",
                    height: "40px",
                    border: "1px solid black",
                    borderRadius: "20px",
                    zIndex: 999,
                    cursor: "pointer"
                }}>X</button>
                <div style={{
                    padding: "15px",
                    height: "inherit",
                    width: "100%",
                    height: "auto",
                    backgroundColor: this.state.localStorageData[key]?.completed
                        ? globalStyles.bartyGreen
                        : globalStyles.yelOrange,
                    color: globalStyles.bartyDark
                }}>
                    <a
                        role="button"
                        href={!this.state.localStorageData[key]?.completed
                            ? `/report/${key.replace("_report_", "")}`
                            : `/view/${this.state.localStorageData[key]?._id}`}
                        style={{
                            color: globalStyles.bartyDark
                        }}>
                        <b>Report ID: {key}</b>
                        <br />
                        <b>Model: {this.state.localStorageData[key]?.general?.model || "Girilmedi"}</b>
                        <br />
                        <b>Marka: {this.state.localStorageData[key]?.general?.brand || "Girilmedi"}</b>
                        <br />
                        <b>Raporun Durumu: {this.state.localStorageData[key]?.completed ? "Tamamlandı" : "Tamamlanmadı"}
                        </b>
                        <br />
                        <b>Başlanma Tarihi: {
                            moment(this.state.localStorageData[key]?.general?.startDate).format("LLL")
                        }
                        </b>

                    </a>
                </div>
            </div >
        )
    }
    render() {
        return (
            <div class="container-fluid" style={{
                backgroundColor: globalStyles.white,
                minHeight: '100vh'
            }}>
                <div class="row">
                    {this.startANewReport()}
                </div>
                <div class="row" style={{ minHeight: "100vh" }}>
                    {this.state.localStorageKeys.length === 0 && <div style={{ textAlign: "center", width: '100%', height: "100%", display: "flex", alignItems: "center", justifyContent: "center" }}> Burada Henüz Bir Rapor Bulunmuyor</div>}
                    {this.state.localStorageKeys.map((item, index) => {
                        return this.renderReports({
                            key: item
                        })
                    })}
                </div>
            </div>
        )
    }
}
export default withRouter(ReportsScreen)