import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";
import ReportsScreen from "../screens/ReportsScreen";
import ReporterScreen from "../screens/ReporterScreen";
import TestAmplifyScreen from "../screens/TestAmplifyScreen";
import ShowReportScreen from "../screens/ShowReportScreen";
import ReporterScreenFinal from '../screens/ReporterScreenFinal';
import ShowReportScreenFinal from '../screens/ShowReportScreenFinal';
function Navigation(props) {
    return (
        <Router>
            <Switch>
                <Route path="/" exact>
                    <TestAmplifyScreen />
                </Route>
                <Route path="/view/:id" component={ShowReportScreenFinal} />
                <Route path="/rapor/:id" component={ShowReportScreenFinal} />
                <Route path="/reporter/:id" component={ReporterScreenFinal} />
                <Route path="/report" exact>
                    <ReportsScreen />
                </Route>
                <Route path="/report/:id" >
                    <ReporterScreen />
                </Route>
            </Switch>
        </Router>
    )
}
function View(props) {
    return (
        <div>
            <h3>IMAGE ID: {props.match.params.id}</h3>
        </div>
    )
}
function Main() {
    return <div>Main</div>
}


export default Navigation;